<template>
  <div class="issue">
    <br>
    <h3>付刚的笔记</h3>
    <div v-for="issue in issueList" 
        :key="issue.id"
        @click="link('/'+issue.type+'/'+issue.id)">
      <br>
      <div class="card border-success">
        <div class="card-header">笔记</div>
        <div class="card-body">
          <h4 class="card-title">{{issue.title}}</h4>
          <p class="card-text">{{issue.info}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods:{
    link:function(to){
      this.$router.push(to)
    }
  },
  data() {
    return {
      issueList: []
    };
  },
  created() {
    const myList = this.GLOBAL;
    this.issueList = myList.filter(l => l.type === "issues").reverse();
  }
}
</script>


